/* payment */
.payment{
	@nest &__title{
		font-size: 34px;
		color: #fff;
		font-weight: 300;
		text-align: center;
		margin: 55px 0 60px;
		@media (max-width: 767px){
			font-size: 27px;
		}
		@nest &--indent{
			margin-bottom: 30px;
		}
	}
	@nest &__desc{
		font-size: 18px;
		color: #fff;
		font-weight: 300;
		text-align: center;
		display: block;
	}
}