/* modal-form */
.modal-form{
	@nest &__desc{
		display: block;
		font-size: 16px;
		color: #fff;
		margin: 0 0 10px;
	}
	@nest &__input{
		width: 100%;
		color: #fff;
		outline: none;
		font-size: 25px;
		font-weight: 300;
		padding-left: 20px;
		min-height: 60px;
		margin-bottom: 30px;
		border: 1px solid #adadad;
		background-color: transparent;
		@nest &::placeholder{
			font-size: 25px;
			color: #838383;
		}
		@nest &--select{
			padding-top: 14px;
			cursor: pointer;
			appearance: none;
			background: resolve('arrow.png') no-repeat 93% center;
			@nest & option{
				color: #000;
			}
		}
	}
	@nest &__btn{
		margin-top: -15px;
	}
}