/* rates-block */
.rates-block{
	max-width: 330px;
	margin: auto;
	padding: 40px 0 30px;
	border-radius: 8px;
	background: #00323b;
	text-align: center;
	@media (max-width: 991px){
		margin-bottom: 20px;
	}
	@nest &--dark{
		background: #002228;
	}
	@nest &--darker{
		background: #141b1c;
	}
	@nest &__title{
		font-size: 60px;
		color: #fff;
		margin: 0 0 35px;
		font-family: var(--font2);
	}
	@nest &__price{
		display: block;
		color: #fff;
		font-size: 16px;
		color: #fff;
		font-weight: 700;
		padding: 30px 0 27px;
		border-radius: 8px;
		margin-bottom: 35px;
		border: 2px solid #2bb0c6;
		background: linear-gradient(to top, #002f37, #124049);
		@nest &--dark{
			background: linear-gradient(to top, #022024, #223f45);
		}
		@nest &--darker{
			background: linear-gradient(to top, #28a4b8, #3ab5ca);
		}
		@nest &--size{
			font-size: 24px;
			margin: 0;
		}
	}
	@nest &__text{
		font-size: 15px;
		color: #fff;
		padding: 0 20px;
		margin: 0 0 20px;
	}
}