/* info */
.info{
	padding: 140px 0 0;
	background: resolve('info.jpg') no-repeat top center;
	-webkit-background-size: cover;
	background-size: cover;
	@nest &__title{
		font-size: 35px;
		color: #fff;
		font-weight: 300;
		margin: 0 0 60px;
		text-align: center;
		@media (max-width: 767px){
			font-size: 27px;
		}
	}
}