/* payment-form */
.payment-form{
	margin-bottom: 385px;
	@media (max-width: 991px){
		margin-bottom: 150px;
	}
	@nest &__desc{
		font-size: 16px;
		color: #fff;
		display: block;
		margin-bottom: 10px;
	}
	@nest &__input{
		width: 100%;
		color: #adadad;
		outline: none;
		min-height: 60px;
		margin-bottom: 35px;
		padding-left: 10px;
		border: 1px solid #adadad;
		background-color: transparent;
		@nest &--area{
			resize: none;
			min-height: 140px;
			padding-top: 10px;
			margin-bottom: 0;
		}
	}
	@nest &__bottom{
		clear: fix;
	}
	@nest &__text{
		float: left;
		font-size: 13px;
		color: #626262;
		margin: 15px 0 0;
	}
	@nest &__btn{
		max-width: 200px;
		float: right;
	}
}