/* info-block */
.info-block{
	margin-bottom: 45px;
	@nest &__title{
		font-size: 18px;
		color: #fff;
		font-weight: 700;
		margin: 0 0 10px;
		position: relative;
		padding-left: 50px;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('ok.png');
			width: width('ok.png');
			height: height('ok.png');
		}
	}
	@nest &__text{
		display: block;
		font-size: 14px;
		color: #fff;
		padding-left: 50px;
	}
}