@import "normalize-css/normalize.css";
@import "fancybox/source/jquery.fancybox.css";
/*! main */
@font-face {
    font-family: 'HelveticaLT-UltraCompressed';
    src: url('../fonts/HelveticaLT-UltraCompressed.woff') format('woff'),
         url('../fonts/HelveticaLT-UltraCompressed.ttf') format('truetype'),
         url('../fonts/HelveticaLT-UltraCompressed.svg#HelveticaLT-UltraCompressed') format('svg');
    font-weight: 400;
    font-style: normal;
}
