/* header */
.header{
	font-size: 0;
	padding: 40px 0 20px;
	@media (max-width: 991px){
		text-align: center;
	}
	@nest &__logo{
		max-width: 195px;
		display: inline-block;
		vertical-align: middle;
		text-align: left;
		@media (max-width: 1199px){
			max-width: 165px;
		}
	}
	@nest &__adress{
		font-size: 13px;
		color: #fff;
		margin: 0;
		max-width: 180px;
		padding-left: 50px;
		position: relative;
		margin-left: 95px;
		font-family: var(--font);
		display: inline-block;
		vertical-align: middle;
		text-align: left;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('geo.png');
			width: width('geo.png');
			height: height('geo.png');
		}
		@media (max-width: 1199px){
			margin-left: 30px;
		}
		@media (max-width: 400px){
			display: block;
			max-width: 180px;
			margin: auto;
			margin-top: 20px;
		}
	}
	@nest &__btn{
		font-size: 13px;
		color: #26a3b8;
		margin-left: 50px;
		border-radius: 4px;
		text-align: left;
		font-family: var(--font);
		text-decoration: none;
		display: inline-block;
		vertical-align: middle;
		padding: 11px 15px 10px;
		border: 1px solid #00ffff;
		transition: background 300ms, color 300ms;
		@nest &:hover{
			background: #0ff;
			color: #000;
		}
		@media (max-width: 1199px){
			margin-left: 30px;
		}
		@media (max-width: 600px){
			display: block;
			max-width: 135px;
			margin: auto;
			margin-top: 20px;
		}
	}
	@nest &__contacts{
		margin-left: 50px;
		max-width: 170px;
		text-align: left;
		display: inline-block;
		vertical-align: middle;
		position: relative;
		padding-left: 35px;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background: resolve('tel.png');
			width: width('tel.png');
			height: height('tel.png');
		}
		@media (max-width: 1199px){
			margin-left: 30px;
		}
		@media (max-width: 991px){
			margin-top: 20px;
		}
		@media (max-width: 600px){
			margin-left: 0;
		}
		@media (max-width: 400px){
			display: block;
			max-width: 170px;
			margin: auto;
			margin-top: 20px;
		}
		@nest &--border{
			margin-left: 30px;
			padding-left: 30px;
			border-left: 1px solid color(#fff alpha(40%));
			@nest &:after{
				display: none;
			}
			@media (max-width: 400px){
				border-left: 0;
				padding-left: 0;
				text-align: center;
				margin: auto;
				margin-top: 20px;
			}
		}
	}
	@nest &__city{
		color: #fff;
		display: block;
		font-size: 13px;
		font-weight: 700;
		font-family: var(--font);
	}
	@nest &__number{
		color: #fff;
		display: block;
		font-size: 16px;
		font-weight: 900;
		font-family: var(--font);
	}
}