/* store */
.store{
	background: #121212;
	@nest & .container{
		padding-bottom: 100px;
		background: resolve('store-bg.jpg') no-repeat bottom right;
		@media (max-width: 991px){
			background: transparent;
			text-align: center;
		}
		@media (max-width: 512px){
			padding-bottom: 20px;
		}
	}
	@nest &__title{
		font-size: 37px;
		color: #fff;
		margin: 0;
		font-weight: 300;
		padding: 135px 0 0;
		max-width: 630px;
		@media (max-width: 1199px){
			max-width: 500px;
		}
		@media (max-width: 991px){
			max-width: 100%;
		}
		@media (max-width: 512px){
			font-size: 25px;
			padding-top: 40px;
		}
	}
	@nest &__text{
		font-size: 23px;
		color: #fff;
		font-weight: 300;
		margin: 40px 0 45px;
		max-width: 630px;
		@nest &--bold{
			font-weight: 700;
		}
		@media (max-width: 1199px){
			max-width: 500px;
		}
		@media (max-width: 991px){
			max-width: 100%;
		}
		@media (max-width: 512px){
			font-size: 17px;
		}
	}
	@nest &__link{
		max-width: 255px;
		margin-right: 30px;
		display: inline-block;
		vertical-align: middle;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@nest &:last-child{
			margin-right: 0;
			@media (max-width: 512px){
				margin: auto;
			}
		}
		@media (max-width: 1199px){
			max-width: 200px;
		}
		@media (max-width: 512px){
			display: block;
			margin: auto;
		}
	}
}