/* billboard */
.billboard{
	min-height: 880px;
	background-color: #000;
	background-image: resolve('billboard.jpg');
	background-repeat: no-repeat;
	background-position: center bottom;
	@media (max-width: 767px){
		min-height: 600px;
		background: #000;
	}
	@nest &--payment{
		position: relative;
		background-image: resolve('payment.jpg');
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			background: resolve('bot2.png');
			height: height('bot2.png');
		}
	}
	@nest &__text{
		font-size: 16px;
		color: #fff;
		font-weight: 300;
		text-align: center;
		margin: 75px 0 40px;
		@media (max-width: 767px){
			font-size: 14px;
		}
	}
	@nest &__title{
		font-size: 34px;
		color: #fff;
		margin: 0;
		font-weight: 300;
		text-align: center;
		@nest &--bold{
			font-weight: 500;
		}
		@media (max-width: 767px){
			font-size: 20px;
		}
	}
}