/* work-block */
.work-block{
	margin-bottom: 120px;
	text-align: center;
	@media (max-width: 767px){
		margin-bottom: 70px;
	}
	@nest &__image{
		margin-bottom: 40px;
	}
	@nest &__text{
		margin: 0;
		color: #fff;
		font-size: 18px;
		display: block;
		@media (max-width: 767px){
			font-size: 16px;
		}
	}
	@nest &__title{
		margin: 55px 0 25px;
		font-size: 17px;
		color: #fff;
		font-weight: 700;
		text-transform: uppercase;
	}
}