/* rates */
.rates{
	padding: 20px 0 0;
	background-color: #000;
	background-image: resolve('rates.png');
	background-repeat: no-repeat;
	background-position: center bottom;
	@nest &__title{
		font-size: 35px;
		color: #fff;
		font-weight: 300;
		margin: 0 0 50px;
		text-align: center;
		@media (max-width: 767px){
			font-size: 27px;
		}
	}
}