/* rates-bottom */
.rates-bottom{
	padding-bottom: 90px;
	@media (max-width: 991px){
		padding-bottom: 30px;
	}
	@nest &--bg{
		max-height: 270px;
		background: resolve('bot.jpg') no-repeat top center;
		-webkit-background-size: cover;
		background-size: cover;
		@media (max-width: 991px){
			max-height: none;
		}
		@nest & .rates-bottom__title{
			margin-top: 0;
			position: relative;
			top: -180px;
			@media (max-width: 991px){
				top: 0;
			}
		}
		@nest & .rates-bottom__content{
			position: relative;
			top: -180px;
			@media (max-width: 991px){
				top: 0;
			}
		}
	}
	@nest &__title{
		font-size: 36px;
		color: #fff;
		font-weight: 300;
		text-align: center;
		margin: 110px 0 80px;
	}
	@nest &__text{
		font-size: 21px;
		color: #fff;
		font-weight: 300;
		margin: 0 0 20px;
		padding-right: 50px;
	}
}