/* modal */
.modal{
	display: none;
	padding: 45px 50px;
	background: #1b1b1b;
	@media (max-width: 512px){
		padding: 15px;
	}
	@nest &__title{
		font-size: 34px;
		color: #fff;
		font-weight: 300;
		color: #fff;
		text-align: center;
		margin: 0 0 40px;
		white-space: nowrap;
		@media (max-width: 512px){
			font-size: 27px;
		}
	}
}
.fancybox-skin{
	padding: 0 !important;
}
.fancybox-close{
	right: -40px;
	top: -40px;
	background: resolve('close.png');
	width: width('close.png');
	height: height('close.png');
}