/* work */
.work{
	background: #000;
	padding: 150px 0 0;
	position: relative;
	@media (max-width: 767px){
		padding-top: 70px;
	}
	@nest &:after{
		position: absolute;
		content: '';
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		background: resolve('bg.png');
		height: height('bg.png');
	}
	@nest &__title{
		font-size: 35px;
		color: #fff;
		font-weight: 300;
		margin-bottom: 65px;
		text-align: center;
		@media (max-width: 767px){
			font-size: 27px;
		}
	}
}