/* rates-form */
.rates-form{
	max-width: 490px;
	margin-right: 0;
	margin-left: auto;
	@media (max-width: 991px){
		margin: 20px auto 0;
	}
	@nest &__input{
		width: 100%;
		outline: none;
		color: #4e4e4e;
		font-size: 29px;
		padding-left: 20px;
		min-height: 60px;
		margin-bottom: 10px;
		border: 1px solid #fff;
		background-color: transparent;
		@nest &::placeholder{
			color: #4e4e4e;
			font-size: 29px;
			@media (max-width: 767px){
				font-size: 18px;
			}
		}
		@media (max-width: 767px){
			font-size: 18px;
		}
	}
	@nest &__btn{
		font-size: 18px;
		color: #fff;
		border: 0;
		outline: none;
		padding: 0;
		width: 100%;
		margin-top: 10px;
		background: #2bbad2;
		min-height: 60px;
		font-weight: 700;
		font-family: var(--font);
		text-transform: uppercase;
		transition: box-shadow 300ms;
		@nest &:hover{
			-webkit-box-shadow: 0px 0px 22px 0px rgba(113,189,199,1);
			-moz-box-shadow: 0px 0px 22px 0px rgba(113,189,199,1);
			box-shadow: 0px 0px 22px 0px rgba(113,189,199,1);
		}
		@media (max-width: 767px){
			font-size: 16px;
		}
		@nest &.modal-form__btn{
			margin-top: -5px;
		}
	}
}