/* nav-list */
.nav-list{
	padding: 0;
	margin: 0;
	text-align: center;
	list-style: none;
	@media (max-width: 991px){
		padding-top: 10px;
	}
	@nest &--bot{
		display: block !important;
		@nest & .nav-list__item{
			margin: 0 0 0 30px;
			@media (max-width: 991px){
				margin-left: 0;
			}
		}
		@nest & .nav-list__link{
			font-size: 12px;
		}
	}
	@nest &__item{
		margin: 0 20px;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 991px){
			display: block;
			margin: 10px 0;
		}
	}
	@nest &__link{
		font-size: 16px;
		color: #fff;
		text-decoration: none;
		font-family: var(--font);
		transition: color 300ms;
		@nest &--active{
			color: #2bbad2;
		}
		@nest &:hover{
			color: #2bbad2;
		}
	}
}