/* nav-toggle */
.nav-toggle{
	text-align: center;
	@nest &__title{
		font-size: 16px;
		color: #fff;
		margin-top: 3px;
		margin-bottom: 0;
		margin-right: 10px;
		display: inline-block;
		vertical-align: middle;
	}
	@nest &__btn{
		border: 0;
		outline: none;
		width: 31px;
		padding: 0;
		position: relative;
		display: inline-block;
		vertical-align: middle;
		background-color: transparent;
	}
	@nest &__line{
		display: block;
		width: 100%;
		height: 6px;
		background: #fff;
		margin-bottom: 2px;
		-webkit-transition: all 300ms;
		transition: all 300ms;
		border-radius: 7px;
	}
	@nest &--open{
		@nest & .nav-toggle__line{
			@nest &:first-child{
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				transform: rotate(-45deg);
			}
			@nest &:nth-child(2){
				display: none;
			}
			@nest &:nth-child(3){
				position: absolute;
				top: 0;
				left: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				transform: rotate(45deg);
			}
		}
	}
	@media (min-width: 991px){
		display: none;
	}
}