/* steps-block */
.steps-block{
	width: 385px;
	height: 385px;
	display: block;
	margin: auto;
	border-radius: 50%;
	border: 1px solid #fff;
	position: relative;
	@media (max-width: 991px){
		margin: 10px auto;
	}
	@media (max-width: 512px){
		width: 250px;
		height: 250px;
	}
	@nest &__content{
		position: absolute;
		content: '';
		left: 0;
		right: 0;
		top: 50%;
		padding: 0 25px;
		transform: translateY(-50%);
	}
	@nest &--left{
		float: left;
		@media (max-width: 991px){
			float: none;
		}
	}
	@nest &--right{
		float: right;
		@media (max-width: 991px){
			float: none;
		}
	}
	@nest &__title{
		font-size: 36px;
		color: #fff;	
		font-weight: 700;
		margin-bottom: 25px;
		@media (max-width: 512px){
			font-size: 27px;
			margin-bottom: 15px;
		}
	}
	@nest &__text{
		font-size: 24px;
		color: #fff;
		display: block;
		@media (max-width: 512px){
			font-size: 17px;
		}
	}
}