/* about */
.about{
	padding: 55px 0 0;
	@nest &__text{
		margin-bottom: 450px;
		text-align: center;
		@media (max-width: 991px){
			margin-bottom: 200px;
		}
	}
	@nest &__title{
		font-size: 34px;
		color: #fff;
		font-weight: 300;
		margin: 0 0 30px;
	}
	@nest &__desc{
		font-size: 16px;
		color: #fff;
		font-weight: 300;
		line-height: 24px;
		margin: 0 0 30px;
	}
}