/* payment-table */
.wrap-table{
	z-index: 99;
	position: relative;
	margin-bottom: 340px;
	@media (max-width: 991px){
		overflow-x: auto;
	}
}
.payment-table{
	min-width: 910px;
	margin: 35px 0 0;
	@nest &__row{
		@nest &--indent-bot{
			padding-bottom: 10px;
		}
	}
	@nest &__col{
		position: relative;
		@nest &--border{
			border-top: 1px solid color(#969696 alpha(50%));
			border-bottom: 1px solid color(#969696 alpha(50%));
		}
		@nest &--lg{
			width: 290px;
		}
		@nest &--sm{
			width: 250px;
		}
		@nest &--width{
			width: 220px;
		}
	}
	@nest &__content{
		width: 220px;
		background: #00323b;
		margin-right: auto;
		text-align: center;
		@nest &--after{
			@nest &:after{
				position: absolute;
				content: '';
				top: 100%;
				height: 35px;
				width: 100%;
				left: 0;
				z-index: -1;
				background: #00323b;
				border-bottom-left-radius: 8px;
				border-bottom-right-radius: 8px;
			}
			@nest &-dark{
				@nest &:after{
					background: #002228;
				}
			}
			@nest &-darker{
				@nest &:after{
					background: #141b1c;
				}
			}
		}
		@nest &--abs{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background: #00323b resolve('ok.png') no-repeat center;
			@nest &-dark{
				background: #00323b;
			}
			@nest &-darker{
				background: #002228;
			}
		}
		@nest &--dark-bg{
			background: #002228;
		}
		@nest &--darker-bg{
			background: #141b1c;
		}
		@nest &--dark{
			background: #002228 resolve('ok.png') no-repeat center;
		}
		@nest &--darker{
			background: #141b1c resolve('ok.png') no-repeat center;
		}
		@nest &--top{
			border-top-left-radius: 8px;
			border-top-right-radius: 8px;
		}
		@nest &--indent-bot{
			padding-bottom: 10px;
		}
	}
	@nest &__title{
		font-size: 30px;
		color: #fff;
		margin: 0;
		font-weight: 700;
		padding: 40px 0 30px;
		text-transform: uppercase;
	}
	@nest &__text{
		font-size: 16px;
		color: #fff;
		display: block;
		padding: 18px 0;
	}
}